	module.exports = {"data":{"markdownRemark":{"html":"<p>doopoll believes that organisations thrive when they understand the people they care about. We build tools and services that help organisations do that better.</p>\n<p>We're looking for a Sales Executive to be there at the beginning of our sales team in Cardiff. Whilst we're looking for a junior, you will be given opportunity to grow and potentially lead within the company.</p>\n<p>Having won the Sir Michael Moritz Start-up of the Year in 2016 and with investment from Angel and Venture Capital Investors this is an opportunity to have a real impact on a business that is scaling quickly.  </p>\n<p>Whilst our sales team is based in Cardiff, doopoll is focused on extensive growth across Europe in 2018, so the role could offer the opportunity for occasional travel. However, the day to day location will be in fantastic offices in central Cardiff.</p>\n<h3>Essential requirements</h3>\n<ul>\n<li>Passionate communicator</li>\n<li>Positive, can do attitude</li>\n<li>Quick learner and thinker</li>\n<li>Very well organised</li>\n<li>Learns from mistakes</li>\n<li>Willingness to develop as an all round team member</li>\n<li>Commitment to personal and professional development</li>\n<li>Comfortable leading your own work with guidance</li>\n</ul>\n<h3>Desirable requirements</h3>\n<ul>\n<li>Fluency in European language/s other than English</li>\n<li>Proof of previous sales success</li>\n<li>Previous experience selling software / SaaS products</li>\n<li>Genuinely enjoys the challenge of meeting and beating targets</li>\n</ul>\n<h3>Responsibilities</h3>\n<ul>\n<li>Selling doopoll's Plus and Super packages over the phone and Skype video</li>\n<li>Contributing to the sales strategy</li>\n<li>Helping to train colleagues (and be trained by them too)</li>\n<li>Capturing user insights to help develop the product and make it easier to sell</li>\n<li>Representing the business at occasional events</li>\n<li>Keeping doopoll's systems up-to-date with relevant notes and feedback from sales calls</li>\n</ul>\n<p>If you would like to apply for the position please send an email to <a href=\"mailto:steve@doopoll.co\">steve@doopoll.co</a> that explains why you think you would be a good fit for our team. Where possible, include examples of sales roles you've undertaken and performance versus targets, along with a short paragraph on the best book you've read this year.</p>","fields":{"slug":"/jobs/sales-executive-oct-2017/"},"frontmatter":{"title":"Sales Executive","lastUpdated":"03.10.2017","closingDate":"01.11.2017","salary":"£18,000 - £20,000pa"}}},"pathContext":{"slug":"/jobs/sales-executive-oct-2017/"}}

